body {
  font-family: 'Cairo', sans-serif;
}

.page {
  min-height: 100vh;
  .page-content {
    width: calc(100% - 250px);
    min-height: 100vh;
    transition: 0.3s;
    will-change: width;
    float: right;
    background-color: #f0f1f3;
    &.no-sidebar {
      width: 100%;
    }
  }
}